import { NgModule } from '@angular/core';
import {
  Routes,
  RouterModule
} from '@angular/router';

import { MailComponent } from './mail.component';
import { AppMailJourneyListingComponent, AppMailTemplateListingComponent } from './_components'

const routes: Routes = [
  {
    path: '',
    redirectTo: 'journeys'
  },
  {
    path: '',
    component: MailComponent,
    data: {
      title: 'Mail'
    },
    children: [
      {
        path: 'journeys',
        component: AppMailJourneyListingComponent,
        data: {
          title: 'Journeys'
        }
      },
      {
        path: 'templates',
        component: AppMailTemplateListingComponent,
        data: {
          title: 'Templates'
        }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MailRouting {}



