import { Component, OnInit } from '@angular/core';
import { TemplateService } from '@services/httpServices/template.service'

@Component({
  selector: 'app-mail-template-listing',
  templateUrl: './mail-template-listing.html',
  styleUrls: ['./mail-template-listing.scss']
})
export class AppMailTemplateListingComponent implements OnInit {
  create_method: string;
  my_template: any;
  predefined_template: any;
  constructor(public templateService: TemplateService) {
    this.create_method = 'from_blank';
    this.my_template = {
      loading: true,
      is_empty: false,
      data: [],
      page: 1,
      total: 0
    };
    this.predefined_template = {
      loading: true,
      is_empty: false,
      data: [],
      page: 1,
      total: 0
    };
  }
  ngOnInit() {
    this.loadMytemplate();
  }
  loadMytemplate(): void {
    this.my_template.loading = true;
    this.templateService.getMyTemplates({
      // user_id:...
      page: this.my_template.page
    }).subscribe(res => {
      this.my_template = {
        ...this.my_template,
        loading: false,
        data: [...this.my_template.data, ...res.data],
        page: this.my_template.page++,
        total: res.total,
        is_empty: res.total === 0
      }
    })
  }
  loadPredefinedtemplate(): void {
    this.my_template.loading = true;
    this.templateService.getPredefinedTemplates({
      // user_id:...
      page: this.predefined_template.page
    }).subscribe(res => {
      this.predefined_template = {
        ...this.predefined_template,
        loading: false,
        data: [...this.predefined_template.data, ...res.data],
        page: this.predefined_template.page++,
        total: res.total,
        is_empty: res.total === 0
      }
    })
  }
  changeCreateMethod(create_method: string) {
    this.create_method = create_method;
    if (create_method === 'from_predefined') {
      if (this.predefined_template.data.length === 0 && this.predefined_template.is_empty === false){
        this.loadPredefinedtemplate()
      }
    }
  }

}



