import { Component, OnInit } from '@angular/core';
import { JourneyService } from '@services/httpServices/journey.service'

@Component({
  selector: 'app-mail-journey-listing',
  templateUrl: './mail-journey-listing.html',
  styleUrls: ['./mail-journey-listing.scss']
})
export class AppMailJourneyListingComponent implements OnInit {
  config_table: any;
  constructor(public journeyService: JourneyService) {
    this.config_table = {
      service: this.journeyService,
      function: 'getJourneys',
      params: {}
    }
  }
  ngOnInit() {
  }

}



