import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HttpModule } from '@angular/http';

import { MailComponent } from './mail.component';
import { MailRouting } from './mail.routing';

import { JourneyService } from '@services/httpServices/journey.service'
import { TemplateService } from '@services/httpServices/template.service';


// Import components
import {
  AppTableComponent,
  AppWidgetTemplateComponent,
  AppWidgetTemplateAddComponent
} from '../../components';

const APP_COMPONENTS = [
  AppTableComponent,
  AppWidgetTemplateComponent,
  AppWidgetTemplateAddComponent
];

import {
  AppMailJourneyListingComponent,
  AppMailTemplateListingComponent
} from './_components'

const MAIL_MODULES_COMPONENTS = [
  AppMailJourneyListingComponent,
  AppMailTemplateListingComponent
];


@NgModule({
  imports: [
    CommonModule,
    HttpModule,
    MailRouting
  ],
  declarations: [
    MailComponent,
    ...APP_COMPONENTS,
    ...MAIL_MODULES_COMPONENTS,
  ],
  providers: [
    JourneyService,
    TemplateService
  ]
})
export class MailModule { }



